import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from "ngx-spinner";
import { Router } from "@angular/router";
import { ApiService } from '../../api.service';

@Component({
  selector: 'app-tv-edit',
  templateUrl: './tv-edit.component.html',
  styleUrls: ['./tv-edit.component.css']
})
export class TvEditComponent implements OnInit {
	data: any = {};
	isSuccessAlert: any = false;
	isDangerAlert: any = false;
  constructor(
  	public spinner: NgxSpinnerService,
  	public api: ApiService,
  	public router: Router
  	) { 
  	this.data = this.router.getCurrentNavigation().extras.state.data;
  	this.data.tv_is_live = (this.data.tv_is_live=='1')?true:false;
  }

  ngOnInit(): void {
  }

  saveProgram(){
		this.spinner.show();
		this.api.provideData({
			type: 'settv',
			tv_title: this.data.tv_title,
			tv_desc: this.data.tv_desc,
			tv_url: this.data.tv_url,
			tv_is_live: this.data.tv_is_live,
			tv_id: this.data.tv_id,
			param: 'update'
		}, 'data-adm-api').subscribe((resp)=>{
			this.isSuccessAlert = true;
			this.spinner.hide();
			setTimeout(()=>{ 
					    this.moveTo('tv');

					 }, 2000);
		});
	  	
	}

	moveTo(url){
		this.router.navigate(['/'+url]);
	}

}
