import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from "ngx-spinner";
import { Router } from "@angular/router";
import { ApiService } from '../../api.service';

@Component({
  selector: 'app-tv-add',
  templateUrl: './tv-add.component.html',
  styleUrls: ['./tv-add.component.css']
})
export class TvAddComponent implements OnInit {
	tv_title: any =  '';
	tv_desc: any =  '';
	tv_url: any = '';
	tv_is_live: any = '';
	isSuccessAlert: any = false;
	isDangerAlert: any = false;
	resp: any;
  constructor(
  	public spinner: NgxSpinnerService,
  	public api: ApiService,
  	public router: Router
  	) { }

  ngOnInit(): void {
  }

  saveProgram(){
		this.spinner.show();
		this.api.provideData({
			type: 'settv',
			tv_title: this.tv_title,
			tv_desc: this.tv_desc,
			tv_url: this.tv_url,
			tv_is_live: this.tv_is_live,
			param: 'create'
		}, 'data-adm-api').subscribe((resp)=>{
			this.isSuccessAlert = true;
			this.spinner.hide();
			setTimeout(()=>{ 
					    this.moveTo('tv');

					 }, 2000);
		});
	  	
	}

	moveTo(url){
		this.router.navigate(['/'+url]);
	}

}
