import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TvComponent} from './tv.component';
import { TvAddComponent} from './tv-add.component';
import { TvEditComponent} from './tv-edit.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Daftar data video AMEG TV'
    },
    children: [
      {
        path: '',
        component: TvComponent,
      },
      {
        path: 'tv-add',
        component: TvAddComponent,
        data: {
          title: 'Tambah data video AMEG TV'
        }
      },
      {
        path: 'tv-edit',
        component: TvEditComponent,
        data: {
          title: 'Ubah data video AMEG TV'
        }
      }
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TvRoutingModule { }
